/* eslint-disable consistent-return */
import nanoid from 'nanoid';
import store from './vuex';
/**
 *  -- xx:xx
 * @param {*} timestamp
 */
const timeParser = (timestamp) => {
    const t = new Date(timestamp);
    return `${t.getFullYear()}${t.getMonth() + 1}${t.getDate()} ${t.getHours()}:${t.getMinutes() >= 10 ? t.getMinutes() : `0${t.getMinutes()}`}`;
};

/**
 * id
 * @param {*} arr
 */
const randomId = (arr) => {
    const allWebJson = arr || [];
    let res = nanoid();
    const r = allWebJson.find(item => item.id === res);
    if (r) {
        res = randomId(arr);
    }
    return res;
};
/**
 * plugins
 * @param {*} context
 */
const getPluginsFromContext = (context) => {
    const res = {};
    context
        .keys()
        .map(context)
        .forEach((a) => {
            res[a.default.name] = a.default;
        });
    return res;
};
/**
 * json
 * @param {*} webJson storewebJson
 * @param {*} fatherId jsonchildNodesstruct
 * @param {*} struct 
 */
const addJson = (webJson, fatherId, struct) => {
    if (webJson.id === fatherId) {
        webJson.childNodes.push(struct);
        return true;
    }
    if (webJson.childNodes) webJson.childNodes.some(item => addJson(item, fatherId, struct));
};
/**
 * idjsoncb
 * @param {*} componentId id
 * @param {*} cb 
 * @param {*} webJson storewebJson
 */
const findJson = (componentId, cb, webJson = store.state.webJson) => {
    if (webJson.id === componentId) {
        cb(webJson);
        return true;
    }
    if (webJson.childNodes) webJson.childNodes.some(item => findJson(componentId, cb, item));
};
/**
 * idjsoncb
 * @param {*} componentId id
 * @param {*} cb 
 * @param {*} webJson storewebJson
 */
const findFJson = (componentId, cb, webJson = store.state.webJson) => {
    if (webJson.childNodes && webJson.childNodes.some(item => item.id === componentId)) {
        cb(webJson);
        return true;
    }
    if (webJson.childNodes) webJson.childNodes.some(item => findFJson(componentId, cb, item));
};
/**
 * mixins
 * @param {*} struct
 * @param {*} component
 */
const drawWeb = (struct, component) => {
    component.$data.struct = struct;
};
/**
 * mixins
 * @param {*} component
 */
const initWeb = (component) => {
    const struct = {
        name: component.name,
        id: component.id,
        options: {
            style: {},
        },
    };
    if (component.name === 'Jcontainer') struct.childNodes = [];
    return struct;
};
/**
 * storewebJson
 * @param {*} struct
 */
const cbToWJ = (struct) => {
    const webJson = store.state.webJson;
    if (webJson.id) {
        const fatherElem = store.state.focusElem;
        const componentList = store.state.componentList;
        addJson(store.state.webJson, fatherElem.id, struct);
        findFJson(struct.id, (s) => {
            s.childNodes.some((item, index) => {
                if (!item.id) {
                    s.childNodes.splice(index, 1);
                    return true;
                }
                return false;
            });
        });
        componentList.some((item, index) => {
            if (!item.cJson.id) {
                componentList.splice(index, 1);
                return true;
            }
            return false;
        });
    } else {
        store.state.webJson = struct;
    }
};
/**
 * storecomponentList
 * @param {*} deleteStruct
 */
const removeFromVuex = (deleteStruct) => {
    if (deleteStruct.childNodes) {
        deleteStruct.childNodes.forEach((item) => {
            removeFromVuex(item);
        });
    }
    const componentList = store.state.componentList;
    componentList.some((item, index) => {
        if (item.id === deleteStruct.id) {
            componentList.splice(index, 1);
            return true;
        }
        return false;
    });
};

const jumpTo = (link) => {
    window.open(link);
};
export { timeParser, randomId, getPluginsFromContext, drawWeb, initWeb, cbToWJ, findJson, findFJson, removeFromVuex, jumpTo };



